<template lang="pug">
.home
  .cardContainer
    #Card(v-for="(card,i) in cards" :style="{'background-image': 'url(' + card.src + ')'}" @click="changePath(card.url)")
      .cardTextContainer
        .CardTitle {{card.title}}
        .CardText {{card.text}}
      .mask
        .maskText 截止倒數： {{parseInt(card.leftDay)}} 天 {{parseInt(card.leftDay)}} 小時 {{parseInt(card.leftMin)}} 分
      .cardIconContainer
        .groupIcon
        .groupNum {{card.group}}
        .viewIcon
        .groupNum {{card.view}}

</template>

<script>
import { db } from '@/components/fireFile.js';
const fStore = db.firestore();
export default {
  data() {
    return {
      cards: [],
      initCard: []
    }
  },
  created(){
    var today = new Date();
    this.$bind('cards',fStore.collection('Home')).then(() => {
      for(let i=0;i<this.cards.length;i++){
        let dl = new Date(this.cards[i].deadline);
        let lfSec = dl-today;
        let leftDay = lfSec / 1000 / 60 / 60 / 24;
        let leftHour = lfSec / 1000 / 60 / 60 % 24;
        let leftMin = lfSec / 1000 / 60 % 60;
        this.cards[i].leftDay = leftDay;
        this.cards[i].leftHour = leftHour;
        this.cards[i].leftMin = leftMin;
      }
      this.initCard = this.cards;
    });
  },
  watch:{
    '$route.query': function(query){
      this.cards = this.initCard;
      function filterByTitle(item) {
        if(item.title.indexOf(query.search)!=-1){
          return true;
        }
        return false;
      }
      var arrByTitle = this.cards.filter(filterByTitle);
      this.cards = arrByTitle;
    }
  },
  methods:{
    changePath: function(url){
      if(url){
        this.$router.push(url);
      }
    }
  }
}
</script>

