<template lang="pug">
.chatroom
    #Optionbar
        .option(v-for='(menu,i) in options' :key="i")
            a(v-if="i==0" :href='menu.link') {{menu.name}}
            router-link(v-if="i!=0" :to='menu.link') {{menu.name}}
    #Chatbody
        transition(name="page" mode="out-in")
            router-view
    #Botroom
        .selfnote
        .botsay(v-if="botsay") {{botsay.que}}
        button.selectboxes(v-if="selectors" v-for="(s,i) in selectors" @click="answer(s.que)") 
            .btnTit(v-if="selectors.length>1") {{i+1}}.
            .btnTxt {{s.sel}}
        input.botinput(v-model="botInput" placeholder="SAY..." @keyup.enter="checkAns")
        button.botinputBtn(@click="checkAns") >
</template>

<script>
import { db } from '@/components/fireFile.js';
const fStore = db.firestore();
export default {
    components: {
    },
    data() {
        return {
            options: [
                {name: "聊天室",link: "/chat"},
                {name: "專案內容",link: "/info"},
                {name: "筆記本",link: "/note"},
                {name: "相簿",link: "/photo"},
                {name: "成員",link: "/member"},
                {name: "個人聊天室",link: ""}
            ],
            botsay: "",
            botsayTemp: [],
            selectors: [],
            queStep: 0,
            botInput: "",
            allAns: {}
        }
    },
    mounted(){
        this.$bind('botsay',fStore.collection('BotSentence').doc('1'));
        this.$bind('allAns',fStore.collection('BotAnswer'));
    },
    watch:{
        'botsay': function(){
            if(this.botsay)
                this.$bind('selectors',fStore.collection('BotAnswer').where("id", "in", this.botsay.sel));
        },
        'botsayTemp': function(temp){
            this.botsay = temp[0];
        },
        'selectors': function(sel){
            if(!sel){
                this.$bind('selectors',fStore.collection('BotAnswer').doc("1"));
            }
        }
    },
    methods: {
        checkAns: function(){
            var botInput = this.botInput;
            if(botInput==1||botInput==2||botInput==3){
                botInput = this.selectors[botInput-1].sel;
            }
            function isCherries(fruit) { 
                return fruit.sel == botInput;
            }
            let theSel = this.allAns.find(isCherries);
            
            if(theSel){
                this.$bind('botsayTemp',fStore.collection('BotSentence')
                    .where("id", "==", theSel.que))
                    .then(() => {
                        this.botInput = "";
                });
            }
            else{
                this.$bind('botsay',fStore.collection('BotSentence').doc('1'))
            }
        },
        answer: function(id){
            this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", id))
        }
    }
}
</script>

<style lang="sass" scoped>
@import "@/style/commom.sass"
.chatroom
    display: flex
    flex-wrap: wrap
    justify-content: space-around
#Optionbar
    width: 33%
#Chatbody
    width: 33%
#Botroom
    width: 33%
    height: 600px
    border: 1px red solid
.selfnote
    width: 100%
    height: 35%
    border: 1px pink solid
</style>