<template lang="pug">
.chaTest
    .chaTestList(v-if="queStep<5")
        button.chaTestBtn(v-for="t in chatrstiseTest[queStep]" @click="chaTests(t)") {{ansList.indexOf(t.type)+1}}{{t.name}}
    button.chaTestBtn(v-if="queStep<5" @click="nextQue") OK
    .chaTestText {{chaTestBot}}
</template>

<script>

export default {
    data() {
        return{
            queStep: 0,
            chatrstiseTest: [
                [{name: "堅定固執",type: "D"},
                {name: "有說服力善表達",type: "I"},
                {name: "溫和文雅",type: "S"},
                {name: "為人謙虛善於容忍",type: "C"}],
                [{name: "喜歡冒險膽子很大",type: "D"},
                {name: "團隊中活力的來源",type: "I"},
                {name: "自我節制個性穩健",type: "S"},
                {name: "嚴格仔細詳細考究",type: "C"}],
                [{name: "有決心的有決斷力",type: "D"},
                {name: "使人心服善於說服",type: "I"},
                {name: "個性善良追求和諧",type: "S"},
                {name: "處事謹慎小心翼翼",type: "C"}],
                [{name: "有進取心有成就心",type: "D"},
                {name: "俏皮變化性情開朗",type: "I"},
                {name: "待人親切做事誠懇",type: "S"},
                {name: "個性順從善於思考",type: "C"}],
                [{name: "有魅力的有威嚴的",type: "D"},
                {name: "個性樂觀有趣好玩",type: "I"},
                {name: "寬大為懷仁慈友善",type: "S"},
                {name: "追求正確準備充分",type: "C"}]
            ],
            chaTestBot: "",
            ansList: [],
            chaList: [{name: "老虎",score: 0},{name: "孔雀",score: 0},{name: "無尾熊",score: 0},{name: "貓頭鷹",score: 0}]
        }
    },
    methods: {
        nextQue: function(){
            if(this.ansList.length<4){
                this.chaTestBot = "請排序全部的敘述";
                return 0;
            }
            this.chaTestBot = "";
            for(let i=0;i<4;i++){
                let chaType = this.ansList[i];
                let score=7-2*i;
                if(chaType=='D'){
                    this.chaList[0].score+=score;
                }
                if(chaType=='I'){
                    this.chaList[1].score+=score;
                }
                if(chaType=='S'){
                    this.chaList[2].score+=score;
                }
                if(chaType=='C'){
                    this.chaList[3].score+=score;
                }
            }
            this.ansList=[];
            this.queStep+=1;
            this.chaList.sort(function (a, b) {
                return b.score - a.score;
            });
            if(this.queStep==5){
                let cha = this.chaList[0].name;
                this.$emit('finshTest',cha);
            }
        },
        chaTests: function(t){
            var findAns = this.ansList.indexOf(t.type);
            
            if(findAns==-1){
                this.ansList.push(t.type);
            }
            else{
                this.ansList.splice(findAns, 1);
            }
        }
    }
}
</script>

