<template lang="pug">
.Makeresume
    .makeresume
        #Optionbar
            .option(v-for='(menu,i) in options' :key="i") {{menu.name}}
        
        h3(v-if="botsay") {{botsay.que}}
        chaTest(v-if="chaTestStatus" v-on:finshTest="changeTestStatue") 
        
        #ResumeBlock
            input.botinput(v-model="resumeInput" placeholder="請輸入..." @keyup.enter="checkAns")
            button.botinputBtn(@click="checkAns") >
</template>

<script>
import { db } from '@/components/fireFile.js';
import chaTest from '@/components/CharacterTest.vue';
const fStore = db.firestore();
export default {
    components: {chaTest},
    data() {
        return{
            options: [
                {name: "基本資料",status: false,content: ""},
                {name: "人格特質",status: false,content: ""},
                {name: "技能",status: false,content: ""},
                {name: "計畫經歷",status: false,content: ""},
                {name: "sharing",status: false,content: ""},
                {name: "rate",status: false,content: ""}
            ],
            resumeStatus: false,
            chaTestStatus: false,
            charitristic: "",
            resumeInput: "",
            step: 1,
            botsay: {},
            botsayTemp: [],
            allAns: {}
        }
    },
    mounted(){
        this.$bind('botsay',fStore.collection('BotSentence').doc('grAOOI5qIfcvefESpwcq'));
        this.$bind('allAns',fStore.collection('BotAnswer'));
    },
    watch:{
        'botsay': function(){

        },
        'botsayTemp': function(temp){
            this.botsay = temp[0];
        }
    },
    methods: {
        changeTestStatue: function(cha){
            this.chaTestStatus = false;
            this.charitristic=cha;
            this.options[1].content = cha;
            this.botsay.que = "看來"+this.options[0].content+"是個"+cha+"型的人格呢";
            this.step=4;
        },
        addAns: function(){
            if(this.step==1){
                this.options[0].content = this.resumeInput;
            }
        },
        checkAns: function(){
            this.addAns();
            this.resumeInput= "";
            if(this.step==3){
                this.chaTestStatus = true;
            }
            else if(this.step==4){
                if(this.charitristic=="老虎"){
                    this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "tiger"));
                }
                else if(this.charitristic=="孔雀"){
                    this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "bird"));
                }
                else if(this.charitristic=="無尾熊"){
                    this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "bear"));
                }
                else if(this.charitristic=="貓頭鷹"){
                    this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "owl"));
                }
                this.step=5;
                return 0;
            }
            else if(this.step==6){
                this.options[2].content = this.resumeInput;
                this.$router.push({ path: 'myresume' })
            }
            else{
                this.step+=1;
            }
            var botInput = this.resumeInput;
            function isCherries(fruit) { 
                return fruit.sel == botInput;
            }
            let theSel = this.allAns.find(isCherries);
            
            if(theSel){
                console.log("Bingo");
            }
            else{
                this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", "r"+this.step));
            }
            
        },
        answer: function(id){
            this.$bind('botsayTemp',fStore.collection('BotSentence').where("id", "==", id))
        }
    }
}
</script>

<style lang="sass">
.makeresume
    display: flex
    flex-wrap: wrap
    justify-content: space-around
#Optionbar
    width: 33%
#ResumeBlock
    width: 50%
</style>
